.news {
    padding: 35px 0 40px;
    &__item {
        display: flex;
        max-width: 33.3%;
        width: 100%;
        margin-top: 35px;
    }
    &__item-inner {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        border: 1px solid var(--iconColor);
        width: 100%;
        transition: all .2s;
        time {
            display: inline-block;
            font-size: 14px;
            font-weight: 600;
            color: var(--iconColor);
            margin-bottom: 6px;
        }
        p {
            transition: all .2s;
        }
        &:hover {
            border: 1px solid var(--White);
            box-shadow: 0px 13px 26px 0px rgba(0, 0, 0, .2);
            box-sizing: content-box;
            p {
                color: var(--brandColor);
            }
        }
    }
    &__info {
        div:last-child {
            padding: 20px 24px;
        }
    }
    &__img {
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        height: 208px;
        width: 100%;
        margin: -1px;
        padding: 1px;
    }
    .btn-bg {
        display: table;
        margin: 30px auto 0 auto;
    }
}
div.news__img {
    box-sizing: content-box;
}
@media(max-width: 991px) {
    .news {
        &__info {
            div:last-child {
                padding: 20px 15px;
            }
        }
    }
}
@media(max-width: 767px) {
    .news {
        &__item {
            max-width: 100%;
        }
    }
}